import React, { useState, useEffect} from 'react'
import { useForm } from 'react-hook-form'
import { useDispatch, useSelector } from 'react-redux'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Button, FormGroup, Form, Label} from 'reactstrap'
import Uppy from '@uppy/core'
//import classnames from 'classnames'
import { EditorState, ContentState, convertFromHTML } from 'draft-js'
import { Editor } from 'react-draft-wysiwyg'
import { convertToHTML } from 'draft-convert'
import classnames from 'classnames'
import DOMPurify from 'dompurify'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
import '@styles/react/libs/editor/editor.scss'
import { DragDrop } from '@uppy/react'
import UiLoader from '@components/ui-loader'
import { useHistory } from 'react-router-dom'
import { editFeedPost } from '../store/action'
import '@uppy/core/dist/style.css'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import './styles.scss'
import Breadcrumbs from '@components/breadcrumbs'
// import { notifyWarning } from '../../Toast'

// import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'


const WallPostEdit = () => {
  const history = useHistory()
  const dispatch = useDispatch()
  const store = useSelector(state => state.feeds)
  const {register, errors, handleSubmit, reset } = useForm() //register, errors,
  const [imageUpload, setImageUpload] = useState('')
  
  
  //const [content, setContent] = useState("")
const [editorState, setEditorState] = useState(() => EditorState.createWithContent(ContentState.createFromBlockArray(convertFromHTML(store?.editPostData?.feed_content)
  
))
    

  )
  const  [convertedContent, setConvertedContent] = useState(null)
  const convertContentToHTML = () => {
    const currentContentAsHTML = convertToHTML(editorState.getCurrentContent())
    setConvertedContent(currentContentAsHTML)
  }
  const createMarkup = (html) => {
    return  {
      __html: DOMPurify.sanitize(html)
    }
    
  }

  const handleEditorChange = (state) => {
    setEditorState(state)
    convertContentToHTML()
  }

// Arabic Editor

const [editor, setEditor] = useState(() => EditorState.createWithContent(ContentState.createFromBlockArray(convertFromHTML(store?.editPostData?.arabic_feed_content ? store?.editPostData?.arabic_feed_content : ''))
  
))
const  [converted, setConverted] = useState(null)

const convertContentHTML = () => {
  const currentContentAsHTML = convertToHTML(editor.getCurrentContent())
  setConverted(currentContentAsHTML)
}

const handleEditor = (state) => {
  setEditor(state)
  convertContentHTML()
}


  useEffect(() => {
    
    if (!Object.keys(store.editPostData)) {
     
      history.replace('/walls')
    } else {
      
     //setValue('post_content', store.editPostData.feed_content)      
      //setContent('post_content', contentBlock)
      setImageUpload(store.editPostData.feed_image)
      
      
    }
  }, [JSON.stringify(store.editPostData)])    
console.log(store)
  useEffect(() => {    
  
    if (store.editSuccessData) { 
      reset()
      setEditorState('')
      setImageUpload('')
      
    }
  }, [store.editSuccessData])
  
  const uppy = new Uppy({
    meta: { type: 'avatar' },
    autoProceed: true,
    restrictions: { maxNumberOfFiles: 1, allowedFileTypes: ['image/*'] }
  })

  uppy.on("file-added", function (file) {
    const reader = new FileReader()
    reader.readAsDataURL(file.data)
    reader.onloadend = function () {
      const base64data = reader.result
      setImageUpload(base64data)
    }
  })
  const dangerouslySetInnerHTML = createMarkup(convertedContent).__html
  const feedtext = (dangerouslySetInnerHTML.length < 20 ? null : dangerouslySetInnerHTML)

  const dangerously = createMarkup(converted).__html
  const text = (dangerously.length < 20 ? null : dangerously)
  const formSubmit = () => {
    if (imageUpload) {
      const params = {
        feed_type: "Post",
        feed_content:feedtext,
        arabic_feed_content:text,
        feed_image: imageUpload
      }      

      const getBase64Image = (img, url) => {
        const canvas = document.createElement("canvas")
        canvas.width = img.width
        canvas.height = img.height
        //const ctx = canvas.getContext("2d")
        //ctx.drawImage(img, 0, 0)
        const getUrl = url.split('.')
        const dataURL = canvas.toDataURL(`image/${getUrl[getUrl.length - 1]}`)
        return dataURL.replace(/^data:image\/(png|jpg);base64,/, "")
      }
      // console.log("Elemnt : ", document.getElementById("post-img-prev-id"))
      const base64 = getBase64Image(document.getElementById("post-img-prev-id"), store.editPostData.feed_image)
      console.log("Base 64 : ",  base64)   
      
        dispatch(editFeedPost(store.editPostData.feed_id, params))
       
    }
  }

  const formHandler = (e) => {
    e.preventDefault()
    if (store.editPostLoader) {
      return
    }

    // if (!imageUpload) {
    //   setImageError(true)
    // } else {
    //   setImageError(false)
    // }

    handleSubmit(formSubmit)()
   
    
  }
  // if ((params.feed_content === null || params.feed_image === '')) {
  //   notifyWarning()
  // } else {
  //   dispatch(
  //     editFeedPost(store.editPostData.feed_id, params))
  // }   
  // const [picker, setPicker] = useState(new Date())

  return (
    <>
          <Breadcrumbs breadCrumbTitle='Walls' breadCrumbParent='Walls' breadCrumbActive='Edit' />
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Edit Post</CardTitle>        
      </CardHeader>
      <CardBody>
        <Row className="wrapper-add-post">
          <Col className="add-post-edit">
            <Form onSubmit={formHandler}>
              <div className="form-label-group add-feed-textarea">
                <FormGroup>
                  <Label className="form-label" for="post_content">
                    <h4>Post Content English</h4>
                  </Label>
                  
                     <Editor 
                        name="post_content"
                        id="post_content"
                        editorState={editorState}
                        onEditorStateChange={handleEditorChange}
                        wrapperClassName="wrapper-class"
                        editorClassName="editor-class"
                        toolbarClassName="toolbar-class"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors['editorState']
                        })}
                      //wrapperStyle={{ width: 800, border: "1px solid black" }}
                   />
                </FormGroup>
               
               
              </div>

              <div className="form-label-group add-feed-textarea">
                <FormGroup>
                  <Label className="form-label" for="post_content">
                  <h3>Post Content Arabic</h3>
                  </Label>
                  
                     <Editor 
                        name="post_content"
                        id="post_content"
                        editorState={editor}
                        onEditorStateChange={handleEditor}
                        wrapperClassName="wrapper-class"
                        editorClassName="editor-class"
                        toolbarClassName="toolbar-class"
                        innerRef={register({ required: true })}
                        className={classnames({
                          'is-invalid': errors['editorState']
                        })}
                      //wrapperStyle={{ width: 800, border: "1px solid black" }}
                   />
                </FormGroup>
               
               
              </div>
             
                <DragDrop
                  uppy={uppy}
                  locale={{
                    strings: {
                      // Text to show on the droppable area.
                      // `%{browse}` is replaced with a link that opens the system file selection dialog.
                      dropHereOr: 'Drop Image or %{browse}',
                      // Used as the label for the link that opens the system file selection dialog.
                      browse: 'browse'
                    }
                  }}
                />
                {imageUpload && (
                  <div className="uploaded-files">
                    <img src={imageUpload} className="post-preview" id="post-img-prev-id" />
                  </div>
                )}
                          
              <FormGroup className="mt-3">
                <div className="d-flex justify-content-start">
                  <Button.Ripple
                    type="submit"
                    color="primary"
                    disabled={store.editPostLoader}
                    className="btn-next"                    
                  >
                    <span className="align-middle d-sm-inline-block d-none">
                      Edit Post
                    </span>
                  </Button.Ripple>
                  {store.editPostLoader && (
                    <div className="ml-2 mt-2">
                      <UiLoader
                        blocking
                      />
                    </div>
                  )}
                  
                </div>
              </FormGroup>
            </Form>
          </Col>
        </Row>
      </CardBody>
    </Card>
    </>
  )
}

export default WallPostEdit