import http from '../../../../configs/httpConfig'
import {notifyError, notifySuccessadd, notifySuccessedit} from "../../../Toast"

//Get all Data Init
export const getAllWallDataInit = () => ({
  type: 'GET_ALL_WALL_DATA_INIT'
})

// Get all Data Fail
export const getAllWallDataFail = () => ({
  type: 'GET_ALL_WALL_DATA_FAIL'
})
//const url = (from && to) ? `/reports/referralReport?from=${from}&to=${to}&page=${page}` : `/reports/referralReport?page=${page}`
// ** Get all Data
export const getAllWallData = (page) => {
  
  return async (dispatch) => {
    dispatch(getAllWallDataInit())
    await http.get(`/feeds/list?page=${page}`).then((response) => {
      console.log(response)
      dispatch({
        type: 'GET_ALL_WALL_POST',
        data: response.data.data
      })
    }).catch(() => {
     // console.log(e)
      dispatch(getAllWallDataFail())
    })
  } 
}

export const Delete = () => {
  return (dispatch) => {
    dispatch({
      type:'WALL_DELETE'
    })
  } 
}

export const deleteFeedPost = (id) => {
  return async (dispatch) => {
    await http.delete(`/feeds/delete/${id}`).then(() => {
      notifyError('successfully deleted')
      dispatch(getAllWallData())      
    }).catch((e) => {     
      notifyError('error')
      dispatch(Delete())
      console.log(e)
    })
  }
}

export const UserFeed = (id) => {  
  return async (dispatch) => {
    dispatch(getAllWallDataInit())
    await http.get(`/feeds/comment?user=${id}`).then((response) => {
      console.log(response)
      dispatch({
        type: 'GET_ALL_WALL_COMMENNT',
        data: response.data.data
      })
    }).catch(() => {
      dispatch(getAllWallDataFail())
    })
  } 
}

export const addPostInit = () => ({
  type: 'ADD_FEED_POST_INIT'
})

export const addPostFail = () => ({
  type: 'ADD_FEED_POST_FAIL'
})

export const addFeedPost = (params) => {
 
  return async (dispatch) => {
    dispatch(addPostInit())
   if (params.feed_content === null) {
  
    notifyError("Eror")
   } else {
    await http.post('/feeds/add', params).then((response) => {
      notifySuccessadd(response.data.message)
      dispatch({
        type: 'ADD_FEED_POST',
        data: response.data.message
      })
      
    }).catch((e) => {
      
      notifyError(e.response.data.message)
      console.log("Error in Feed Post: ", e)
      dispatch(addPostFail())
    })
  }
}
   }
    

export const addPostReset = () => ({
  type: 'RESET_ADD_POST'
})


export const editPostReset = () => ({
  type: 'RESET_EDIT_POST'
})

export const editPostData = (params) => ({
  type: 'EDIT_POST_DATA',
  data: params
})

export const editPostInit = () => ({
  type: 'EDIT_POST_INIT'
})

export const editPostFail = () => ({
  type: 'EDIT_POST_FAIL'
})


export const editFeedPost = (id, params) => {
  return async (dispatch) => {
    dispatch(editPostInit())
    await http.put(`/feeds/edit/${id}`, params).then((response) => {
      console.log(response)
      notifySuccessedit(response.data.message)
      //console.log(response.data.message)
      console.log("Respont : ", response)
      dispatch({
        type: 'EDIT_FEED_POST',
        data: response.data.message
      })
    }).catch((e) => {
      console.log(e)
      notifyError(e.data.message)
      console.log("Error in Feed Post: ", e)
      dispatch(editPostFail())
    })
  }
}

export const wallview = (id) => {
  return async (dispatch) => {
    await http.put(`/feeds/view/${id}`).then((response) => {
      console.log("Respont : ", response)
      dispatch({
        type: 'GET_WALL_VIEW',
        data: response.data.message
      })
    }).catch((e) => {
      console.log("Error in Feed Post: ", e)
      dispatch(editPostFail())
    })
  }
}
export const Feedtogglestatus = (id) => {
  return async (dispatch) => {    
    await http.post(`feeds/toggle_status/${id}`).then((response) => {
      notifySuccessadd('successfully updated')      
     console.log(response)
       dispatch({
        type: 'FEED_TOGGLE_STATUS',
        data: response
      })  
       dispatch(getAllWallData())        
    }).catch((e) => {
      notifyError('error')
      console.log(e)
      console.log("Error in Feed Post: ", e)
      
    })
  }
}